.auth {
  background: #f5f6f8;
  display: flex;
  justify-content: center;
  height: 100vh;

  &__background {
    font-family: Helvetica-Regular;
    display: flex;
    flex-direction: column;
    margin: 167px auto;
    width: 376.5px;
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 16.5px;
  }

  &__logo-title {
    margin-left: 10px;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 28px;
    color: #3D5170;
  }

  &__form {
    width: 376.5px;
    height: 253.5px;
    display: flex;
    flex-direction: column;
    background: #FFFFFF;
    box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12), 0px 5px 5px rgba(90, 97, 105, 0.06), 0px 3.5px 35px rgba(90, 97, 105, 0.1);
    border-radius: 9px;
  }

  &__title {
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 20px;
    text-align: center;
    color: #3d5170;
    margin: 19px 0 30px;
  }

  &__label {
    flex-direction: column;
    display: flex;
    font-style: normal;
    font-weight: normal;
    font-size: 10.5px;
    line-height: 12px;
    color: #495057;
  }

  &__fields {
    margin: 0 18px;
    font-style: normal;
    font-weight: normal;
    font-size: 10.5px;
    line-height: 12px;
    color: #495057;
  }

  &__field {
    margin: 0 0 15px 0;
  }

  &__footer {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 16px 18px;
  }

  &__get-access {
    font-weight: 400;
    cursor: pointer;
    font-style: normal;
    font-size: 10.5px;
    line-height: 12px;
    color: #007BFF;
    border: none;
    background: white;
    padding: 0;
  }
}
